<template>
  <div>
    <h1></h1>
    <hr>
    <h2> - &lt;uk-form&gt;</h2>

    <uk-form :stacked="formStacked"
             :horizontal="formHorizontal">
      <uk-field name="username"
                label=""
                icon="user">
        <uk-input name="username"
                  id="username"></uk-input>
      </uk-field>
      <uk-field name="github"
                label="Github"
                icon="github">
        <uk-input name="github"
                  id="github"></uk-input>
      </uk-field>
    </uk-form>

    <h3>&lt;uk-form&gt; </h3>
    <table class="uk-table uk-table-striped uk-table-condensed">
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th class="uk-width-2-6"></th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td><code>stacked</code></td>
        <td><code>Boolean</code></td>
        <td><code>false</code></td>
        <td></td>
        <td class="uk-form">
          <input type="checkbox" v-model="formStacked"/>
        </td>
      </tr>
      <tr>
        <td><code>horizontal</code></td>
        <td><code>Boolean</code></td>
        <td><code>false</code></td>
        <td></td>
        <td class="uk-form">
          <input type="checkbox" v-model="formHorizontal"/>
        </td>
      </tr>
      </tbody>
    </table>
    <uk-alert>
      &lt;uk-field&gt; form form
    </uk-alert>
    <p>
      <code-block>{{ usages.form }}</code-block>
    </p>

    <h2> - &lt;uk-password&gt; </h2>
    <hr>
    <div class="uk-form">
      <uk-password name="user-password"
                   :show-label="showLabel"
                   :hide-label="hideLabel"></uk-password>
    </div>
    <p>
      <code-block>{{ usages.pwd }}</code-block>
    </p>
    <h3></h3>
    <hr>
    <table class="uk-table uk-table-striped uk-table-condensed">
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th class="uk-width-2-6"></th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td><code>showLabel</code></td>
        <td><code>String</code></td>
        <td><code>undefined</code></td>
        <td>""</td>
        <td class="uk-form">
          <input type="text"
                 v-model="showLabel"
                 class="uk-width-1-1"/>
        </td>
      </tr>
      <tr>
        <td><code>hideLabel</code></td>
        <td><code>String</code></td>
        <td><code>undefined</code></td>
        <td>""</td>
        <td class="uk-form">
          <input type="text"
                 v-model="hideLabel"
                 class="uk-width-1-1"/>
        </td>
      </tr>

      </tbody>
    </table>

    <h2> - &lt;uk-select&gt;</h2>
    <hr>
    <p class="uk-form">
      <uk-select :items="['1','2','3','4','5']">
      </uk-select>
    </p>

    <p>
      <code-block>{{ usages.select }}</code-block>
    </p>

    <h2> - &lt;uk-progress&gt;</h2>
    <hr>
    <h3></h3>
    <p>
      <uk-progress :striped="striped"
                   :show-value="showValue"
                   :size="size"
                   :color="color"
                   :value="percent">
      </uk-progress>
    </p>

    <p>
      <code-block>{{ usages.progress }}</code-block>
    </p>

    <h3></h3>
    <table class="uk-table uk-table-striped uk-table-condensed">
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th class="uk-width-2-6"></th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td><code>value</code></td>
        <td><code>Number</code></td>
        <td><code>0</code></td>
        <td>0~100</td>
        <td class="uk-form">
          <input v-model="percent"
                 class="uk-width-1-1"
                 min="0"
                 max="100"
                 type="number"/>
        </td>
      </tr>
      <tr>
        <td><code>color</code></td>
        <td><code>String</code></td>
        <td><code>undefined</code></td>
        <td></td>
        <td class="uk-form">
          <select v-model="color" class="uk-width-1-1">
            <option>undefined</option>
            <option>success</option>
            <option>warning</option>
            <option>danger</option>
          </select>
        </td>
      </tr>
      <tr>
        <td><code>size</code></td>
        <td><code>String</code></td>
        <td><code>undefined</code></td>
        <td></td>
        <td class="uk-form">
          <select v-model="size" class="uk-width-1-1">
            <option>undefined</option>
            <option>mini</option>
            <option>small</option>
          </select>
        </td>
      </tr>

      <tr>
        <td><code>showValue</code></td>
        <td><code>Boolean</code></td>
        <td><code>false</code></td>
        <td></td>
        <td class="uk-form">
          <input type="checkbox" v-model="showValue"/>
        </td>
      </tr>
      <tr>
        <td><code>striped</code></td>
        <td><code>Boolean</code></td>
        <td><code>false</code></td>
        <td></td>
        <td class="uk-form">
          <input type="checkbox" v-model="striped"/>
        </td>
      </tr>

      </tbody>
    </table>

    <h2> &lt;uk-icon-field&gt;</h2>
    <p>
       <code>&lt;uk-icon&gt;</code><code>icon</code>
    </p>
    <div class="uk-form">
      <uk-icon-field icon="calendar">
        <input type="text"/>
      </uk-icon-field>
    </div>
  </div>
</template>
<script>
  import 'uikit/dist/css/components/progress.gradient.css'
  export default{
    data () {
      return {
        formStacked: false,
        formHorizontal: false,
        showLabel: '',
        hideLabel: '',
        percent: 40,
        size: undefined,
        color: undefined,
        striped: false,
        showValue: true,
        usages: {
          pwd: `<uk-password name="user-password"
                   show-label=""
                   hide-label=""></uk-password>`,
          select: `<uk-select :items="[
    {label:'1',value:1},
    {label:'2',value:2},
    {label:'3',value:3},
    {label:'4',value:4},
    {label:'5',value:5}
   ]">
</uk-select>`,
          progress: `<uk-progress
    :striped="true|false"
    :show-value="true|false"
    size="mini|small"
    color="success|warning|danger"
    :value="0-100">
</uk-progress>`,
          form:`<uk-form :stacked="formStacked"
        :horizontal="formHorizontal">
  <uk-field name="username"
            label=""
            icon="user">
    <uk-input name="username"
              id="username"></uk-input>
  </uk-field>
  <uk-field name="github"
            label="Github"
            icon="github">
    <uk-input name="github"
              id="github"></uk-input>
  </uk-field>
</uk-form>`
        }
      }
    }
  }
</script>



