<template>
  <div>
    <h1> - Tabs</h1>
    <hr>
    <h2></h2>
    <uk-tabs :active="tabIndex"
             :layout="tabLayout"
             :center="tabCenter"
             :width="tabWidth">
      <uk-tab label="Tab1">
        {{ getContent() }}
      </uk-tab>
      <uk-tab label="Tab2">
        {{ getContent() }}
      </uk-tab>
      <uk-tab label="Tab3">
        {{ getContent() }}
      </uk-tab>
      <uk-tab :disabled="true" label="Disabled">
        Disabled content
      </uk-tab>
    </uk-tabs>
    <h3></h3>
    <code-block>{{usages.basic}}</code-block>

    <h3></h3>
    <table class="uk-table uk-table-striped uk-table-condensed">
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th style="width:80px;"></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td><code>active</code></td>
        <td><code>Boolean</code></td>
        <td>0</td>
        <td></td>
        <td class="uk-form">
          <input type="number"
                 v-model="tabIndex"
                 class="uk-width-1-1" max="2" min="0"/>
        </td>
      </tr>
      <tr>
        <td><code>layout</code></td>
        <td><code>String</code></td>
        <td>
          <p><code>undefined</code></p>
          
          <ul>
            <li><code>'flip'</code> - </li>
            <li><code>'left'</code> - </li>
            <li><code>'right'</code> - </li>
            <li><code>'bottom'</code> - </li>
          </ul>
        </td>
        <td>
          
        </td>
        <td class="uk-form">
          <select v-model="tabLayout"
                  class="uk-width-1-1">
            <option label="undefined"></option>
            <option>flip</option>
            <option>left</option>
            <option>right</option>
            <option>bottom</option>
          </select>
        </td>
      </tr>
      <tr>
        <td><code>center</code></td>
        <td><code>String</code></td>
        <td><code>undefined</code></td>
        <td></td>
        <td class="uk-text-center">
          <input type="checkbox"
                 v-model="tabCenter"/>
        </td>
      </tr>
      <tr>
        <td><code>width</code></td>
        <td><code>String</code></td>
        <td><code>undefined</code></td>
        <td></td>
        <td class="uk-form">
          <select v-model="tabWidth"
                  class="uk-width-1-1">
            <option>1-2</option>
            <option>1-3</option>
            <option>1-4</option>
            <option>1-5</option>
            <option>1-6</option>
          </select>
        </td>
      </tr>
      </tbody>
    </table>


  </div>
</template>
<script>
  import Chance from 'chance'
  export default {
    data () {
      return {
        tabWidth: '1-3',
        tabCenter: false,
        tabLayout: undefined,
        tabIndex: 0,
        chance: new Chance(),
        usages: {
          basic: `<uk-tabs :active="1"
      layout="flip|left|right|bottom"
      :center="true|false"
      width="1-3">
      <uk-tab label="">
        1
      </uk-tab>
      <uk-tab label="2">
        2
      </uk-tab>

      ...

      <uk-tab :disabled="true" label="">
      </uk-tab>
    </uk-tabs>`
        }
      }
    },
    methods: {
      getContent() {
        return chance.paragraph()
      }
    }
  }
</script>



