<template>
  <div>
    <h1> - &lt;uk-modal&gt;</h1>
    <h2></h2>
    <h3></h3>
    <uk-button @click="open"></uk-button>
    <uk-modal title=""
              ref="dialog">
      <p></p>
    </uk-modal>

    <h3></h3>
    <code-block>{{usages.basic}}</code-block>


    <h2>Lightbox </h2>
    <hr>
    <p> <code>:lightbox="true"</code> lightbox</p>

    <code-block>{{usages.lightbox}}</code-block>
    <uk-button @click="$refs.lightboxDlg.open()">Lightbox</uk-button>
    <uk-modal ref="lightboxDlg"
              :closable="true"
              :lightbox="true">
      <img src="../assets/placeholder_600x400.svg"
           width="600"
           height="400" alt="">
    </uk-modal>


    <h2></h2>
    <hr>
    <p></p>
    <uk-button @click="$refs.blankDlg.open()">Blank</uk-button>
    <uk-modal ref="blankDlg" :closable="true" :blank="true">
      <div class="uk-grid uk-flex-middle"
           data-uk-grid-margin="">
        <div class="uk-width-medium-1-2 uk-height-viewport uk-cover-background uk-row-first"
             style="background-image: url('../assets/placeholder_120x255.svg');"></div>
        <div class="uk-width-medium-1-2 uk-row-first">
          <h1>Headline</h1>
          <div class="uk-width-medium-1-3">
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et
              dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex
              ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu
              fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt
              mollit anim id est laborum.</p>
          </div>
        </div>
      </div>
    </uk-modal>


    <h3></h3>
    <table class="uk-table uk-table-striped uk-table-condensed">
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td><code>keyboard</code></td>
        <td>boolean</td>
        <td>true</td>
        <td>ESC</td>
      </tr>
      <tr>
        <td><code>lightbox</code></td>
        <td>boolean</td>
        <td>true</td>
        <td>lightbox</td>
      </tr>
      <tr>
        <td><code>blank</code></td>
        <td>boolean</td>
        <td>true</td>
        <td></td>
      </tr>

      <tr>
        <td><code>bgclose</code></td>
        <td>boolean</td>
        <td>true</td>
        <td></td>
      </tr>
      <tr>
        <td><code>minScrollHeight</code></td>
        <td>integer</td>
        <td>150</td>
        <td></td>
      </tr>
      <tr>
        <td><code>center</code></td>
        <td>boolean</td>
        <td>false</td>
        <td></td>
      </tr>
      <tr>
        <td><code>modal</code></td>
        <td>boolean</td>
        <td>true</td>
        <td></td>
      </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        usages: {
          basic: `<uk-button @click="$refs.dialog.open()"></uk-button>
<uk-modal title=""
      ref="dialog">
  <p></p>
</uk-modal>`,
          lightbox:`<uk-button @click="$refs.lightboxDlg.open()">Lightbox</uk-button>
<uk-modal ref="lightboxDlg"
          :closable="true"
          :lightbox="true">
  <img src="../assets/placeholder_600x400.svg"
       width="600"
       height="400" alt="">
</uk-modal>`
        }
      }
    },
    methods: {
      open () {
        this.$refs.dialog.open()
      }
    }
  }
</script>



